.paralax {
  min-height: 100%;
  height: 100vh;
}

.paralax-body {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.text {
  color: #fff;
  text-align: center;
  z-index: 2;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.text-content {
  text-transform: uppercase;
  font-family: monospace;
  font-size: 228px;
  font-weight: 900;
  line-height: 120%;
}

.paralax-images {
  width: 120%;
  height: 120%;
  z-index: 1;
  position: absolute;
  top: -10%;
  left: -10%;
}

.paralax-item {
  width: 100%;
  height: 100%;
}

.paralax-star {
  width: 100%;
  height: 100%;
  background: url("car.de953437.jpg") top / cover no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}

.content {
  z-index: 3;
  width: 100%;
  height: 100vh;
  background-color: gray;
  position: relative;
}

